import { Component, Vue } from 'vue-property-decorator'
import { getOrderDetailVO } from '@/api/financail/financailApi'
@Component({
  components: {},
})
export default class detail extends Vue {
  private pageTitle = '交易详情'
  // 订单id
  private id = ''
  // 订单详情
  private data = {}
  public mounted(): void {
    this.id = Vue.ls.get('/financial/detail')
    console.log('id', this.id)
    this.getOrderDetail()
  }
  // 获取交易详情
  private async getOrderDetail() {
    const res = await getOrderDetailVO({ id: this.id })
    if (res.code === 200) {
      this.data = res.data
    }
  }

  private goBack() {
    this.$router.back()
  }
}
